@import url('https://fonts.googleapis.com/css?family=Noto+Sans+JP:400,900&display=swap&subset=japanese');

//フォントサイズ
$font-size-s: 1.0rem;
$font-size-m: 1.4rem;
$font-size-l: 1.6rem;

//マージンサイズ
$base-space: 8px;
$base-space-2: 16px;
$base-space-3: 24px;
$base-space-4: 32px;

//カラーコード
$base-color: #979292; //灰色
$base-color-2: #4d5254; //濃い灰色

* {
  font-family: 'Noto Sans JP', sans-serif;
  box-sizing: content-box;
  font-size: $font-size-s;
}

html, body {
  padding: 0;
  margin: 0;
}

main {
  padding: $base-space-2 0;
}

